// src/shared/ui/SimpleSelect.tsx
/** @jsxImportSource preact */
import { h } from 'preact';
import { useState, useRef, useEffect } from 'preact/hooks';
import { Box, Typography } from '@mui/material';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';

// ÂÆö‰πâÁªÑ‰ª∂Êé•Êî∂ÁöÑpropsÁ±ªÂûã
type SimpleSelectProps = {
    value: string;
    options: Array<{ value: string; label: string }>;
    onChange: (newValue: string) => void;
    placeholder?: string;
    fullWidth?: boolean;
    sx?: object;
};

export function SimpleSelect({ value, options, onChange, placeholder, fullWidth, sx }: SimpleSelectProps) {
    // Áä∂ÊÄÅÔºöÊéßÂà∂‰∏ãÊãâËèúÂçïÊòØÂê¶Â±ïÂºÄ
    const [isOpen, setIsOpen] = useState(false);
    // RefÔºöÁî®‰∫éËé∑ÂèñÁªÑ‰ª∂Ê†πÂÖÉÁ¥†ÁöÑÂºïÁî®Ôºå‰ª•Âà§Êñ≠ÁÇπÂáª‰∫ã‰ª∂ÊòØÂê¶ÂèëÁîüÂú®ÁªÑ‰ª∂Â§ñÈÉ®
    const wrapperRef = useRef<HTMLDivElement>(null);

    // Ëé∑ÂèñÂΩìÂâçÈÄâ‰∏≠È°πÁöÑÊòæÁ§∫Ê†áÁ≠æ
    const selectedLabel = options.find(opt => opt.value === value)?.label || value;

    // Â§ÑÁêÜÂ§ñÈÉ®ÁÇπÂáª‰∫ã‰ª∂
    useEffect(() => {
        function handleClickOutside(event: MouseEvent) {
            // Â¶ÇÊûúËèúÂçïÊòØÊâìÂºÄÁöÑÔºåÂπ∂‰∏îÁÇπÂáªÁöÑ‰ΩçÁΩÆ‰∏çÂú®ÁªÑ‰ª∂ÂÜÖÈÉ®ÔºåÂàôÂÖ≥Èó≠ËèúÂçï
            if (wrapperRef.current && !wrapperRef.current.contains(event.target as Node)) {
                setIsOpen(false);
            }
        }
        // ÁõëÂê¨ÂÖ®Â±ÄÁöÑmousedown‰∫ã‰ª∂
        document.addEventListener('mousedown', handleClickOutside);
        // ÁªÑ‰ª∂Âç∏ËΩΩÊó∂ÁßªÈô§ÁõëÂê¨ÔºåÈò≤Ê≠¢ÂÜÖÂ≠òÊ≥ÑÊºè
        return () => {
            document.removeEventListener('mousedown', handleClickOutside);
        };
    }, [wrapperRef]); // ‰æùËµñÈ°πÊòØrefÊú¨Ë∫´

    // Â§ÑÁêÜÈÄâÊã©‰∏Ä‰∏™ÈÄâÈ°π
    const handleOptionClick = (optionValue: string) => {
        onChange(optionValue); // Ë∞ÉÁî®Áà∂ÁªÑ‰ª∂‰º†ÂÖ•ÁöÑÂõûË∞É
        setIsOpen(false); // ÂÖ≥Èó≠ËèúÂçï
    };

    return (
        <Box
            ref={wrapperRef}
            sx={{
                position: 'relative',
                width: fullWidth ? '100%' : 'auto',
                ...sx
            }}
        >
            {/* ÊòæÁ§∫ÂΩìÂâçÂÄºÁöÑÂå∫Âüü */}
            <Box
                onClick={() => setIsOpen(!isOpen)}
                sx={{
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'space-between',
                    height: '36px',
                    border: '1px solid rgba(0,0,0,0.15)',
                    borderRadius: '6px',
                    padding: '6px 10px',
                    cursor: 'pointer',
                    bgcolor: '#fff',
                    '&:hover': {
                        borderColor: 'rgba(0,0,0,0.4)',
                    },
                }}
            >
                <Typography sx={{ fontSize: 13, color: value ? 'text.primary' : 'text.secondary' }}>
                    {value ? selectedLabel : (<em>{placeholder}</em>)}
                </Typography>
                <ArrowDropDownIcon sx={{ color: 'text.secondary', transform: isOpen ? 'rotate(180deg)' : 'rotate(0deg)', transition: 'transform 0.2s' }} />
            </Box>

            {/* ‰∏ãÊãâÈÄâÈ°πÂàóË°® */}
            {isOpen && (
                <Box
                    sx={{
                        position: 'absolute',
                        top: '100%',
                        left: 0,
                        right: 0,
                        mt: '4px',
                        bgcolor: 'background.paper',
                        border: '1px solid rgba(0,0,0,0.1)',
                        borderRadius: '6px',
                        boxShadow: '0 4px 12px rgba(0,0,0,0.1)',
                        maxHeight: '200px',
                        overflowY: 'auto',
                        zIndex: 1300, // Á°Æ‰øùÂú®ÂÖ∂‰ªñÂÖÉÁ¥†‰πã‰∏ä
                    }}
                >
                    {options.map(option => (
                        <Box
                            key={option.value}
                            onClick={() => handleOptionClick(option.value)}
                            sx={{
                                padding: '8px 12px',
                                fontSize: 13,
                                cursor: 'pointer',
                                '&:hover': {
                                    bgcolor: 'action.hover',
                                },
                                ...(value === option.value && {
                                    bgcolor: 'action.selected',
                                    fontWeight: 500,
                                }),
                            }}
                        >
                            {option.label}
                        </Box>
                    ))}
                </Box>
            )}
        </Box>
    );
}