// src/core/settings/ui/components/TemplateEditorModal.tsx
/** @jsxImportSource preact */
import { h } from 'preact';
import { useState, useEffect, useMemo } from 'preact/hooks';
import { Dialog, DialogTitle, DialogContent, DialogActions, Button, Stack, TextField, Typography, Box, RadioGroup, FormControlLabel, Radio, FormControl, FormLabel, Divider } from '@mui/material';
import { FieldsEditor } from './FieldsEditor';
import type { BlockTemplate, ThemeDefinition, ThemeOverride, TemplateField } from '@core/domain/schema';
import { AppStore } from '@state/AppStore';
import { Notice } from 'obsidian';
import { TemplateVariableCopier } from './TemplateVariableCopier';

type EditMode = 'inherit' | 'override' | 'disabled';

interface Props {
    isOpen: boolean;
    onClose: () => void;
    block: BlockTemplate;
    theme: ThemeDefinition;
    existingOverride: ThemeOverride | null;
}

export function TemplateEditorModal({ isOpen, onClose, block, theme, existingOverride }: Props) {
    const [mode, setMode] = useState<EditMode>('inherit');
    const [localOverride, setLocalOverride] = useState<Partial<ThemeOverride>>({});

    useEffect(() => {
        if (existingOverride) {
            setMode(existingOverride.status === 'disabled' ? 'disabled' : 'override');
            setLocalOverride(structuredClone(existingOverride));
        } else {
            setMode('inherit');
            setLocalOverride({
                fields: structuredClone(block.fields),
                outputTemplate: block.outputTemplate,
                targetFile: block.targetFile,
                appendUnderHeader: block.appendUnderHeader
            });
        }
    }, [isOpen, existingOverride, block]);

    const effectiveBlockForCopier = useMemo<BlockTemplate>(() => {
        return {
            ...block,
            ...localOverride,
            fields: localOverride.fields || block.fields,
        };
    }, [block, localOverride]);

    const handleSave = () => {
        const store = AppStore.instance;
        if (mode === 'inherit') {
            if (existingOverride) store.deleteOverride(block.id, theme.id);
            new Notice(`Â∑≤ËÆæ‰∏∫ÁªßÊâø "${block.name}" ÁöÑÂü∫Á°ÄÈÖçÁΩÆ`);
        } else {
            const dataToSave: Omit<ThemeOverride, 'id'> = {
                blockId: block.id,
                themeId: theme.id,
                status: mode === 'disabled' ? 'disabled' : 'enabled',
                ...localOverride,
            };
            store.upsertOverride(dataToSave);
            new Notice(`Â∑≤‰øùÂ≠ò "${theme.path}" ÂØπ "${block.name}" ÁöÑÈÖçÁΩÆ`);
        }
        onClose();
    };
    
    if (!isOpen) return null;

    const isFormDisabled = mode !== 'override';

    return (
        <Dialog open={isOpen} onClose={onClose} fullWidth maxWidth="md" disablePortal>
            <DialogTitle>
                <Typography>
                    ÈÖçÁΩÆ: <strong>{theme.path}</strong> / <span style={{ color: 'var(--color-accent)' }}>{block.name}</span>
                </Typography>
            </DialogTitle>
            <DialogContent dividers>
                <Stack spacing={3}>
                    <FormControl component="fieldset">
                        <FormLabel component="legend">ÈÖçÁΩÆÊ®°Âºè</FormLabel>
                        <RadioGroup row value={mode} onChange={e => setMode((e.target as HTMLInputElement).value as EditMode)}>
                            <FormControlLabel value="inherit" control={<Radio />} label="ÁªßÊâø" />
                            <FormControlLabel value="override" control={<Radio />} label="Ë¶ÜÂÜô" />
                            <FormControlLabel value="disabled" control={<Radio />} label="Á¶ÅÁî®" />
                        </RadioGroup>
                    </FormControl>
                    <fieldset disabled={isFormDisabled} style={{ border: 'none', padding: 0, margin: 0, opacity: isFormDisabled ? 0.6 : 1 }}>
                        <Stack spacing={3}>
                            <Divider />
                            <Box>
                                <Typography variant="h6" sx={{ fontSize: '1rem', fontWeight: 600, mb: 1 }}>ËæìÂá∫ÁõÆÊ†á</Typography>
                                <Stack spacing={2}>
                                    <TextField label="ÁõÆÊ†áÊñá‰ª∂Ë∑ØÂæÑ" value={localOverride.targetFile || ''} onChange={e => setLocalOverride(o => ({...o, targetFile: e.target.value}))} fullWidth variant="outlined" />
                                    <TextField label="ËøΩÂä†Âà∞Ê†áÈ¢ò‰∏ã (ÂèØÈÄâ)" value={localOverride.appendUnderHeader || ''} onChange={e => setLocalOverride(o => ({...o, appendUnderHeader: e.target.value}))} fullWidth variant="outlined" />
                                </Stack>
                            </Box>
                            <Divider />
                            <Box>
                                <Typography variant="h6" sx={{ fontSize: '1rem', fontWeight: 600, mb: 1.5 }}>Ë°®ÂçïÂ≠óÊÆµ</Typography>
                                <FieldsEditor fields={localOverride.fields || []} onChange={(newFields: TemplateField[]) => setLocalOverride(o => ({ ...o, fields: newFields }))} />
                            </Box>
                            <Divider />
                            <Box>
                                <Stack direction="row" justifyContent="space-between" alignItems="center" sx={{ mb: 1 }}>
                                    <Typography variant="h6" sx={{ fontSize: '1rem', fontWeight: 600 }}>ËæìÂá∫Ê®°Êùø</Typography>
                                    <TemplateVariableCopier block={effectiveBlockForCopier} />
                                </Stack>
                                {/* [‰øÆÊîπ] Á°ÆËÆ§Ë°åÊï∞ËÆæÁΩÆ‰∏∫ 8 */}
                                <TextField multiline rows={8} value={localOverride.outputTemplate || ''} onChange={e => setLocalOverride(o => ({...o, outputTemplate: e.target.value}))} fullWidth variant="outlined" sx={{ fontFamily: 'monospace' }} />
                            </Box>
                        </Stack>
                    </fieldset>
                </Stack>
            </DialogContent>
            <DialogActions>
                <Button onClick={onClose}>ÂèñÊ∂à</Button>
                <Button onClick={handleSave} variant="contained">‰øùÂ≠òÈÖçÁΩÆ</Button>
            </DialogActions>
        </Dialog>
    );
}