// src/core/settings/ui/BlockManager.tsx
/** @jsxImportSource preact */
import { h } from 'preact';
import { useStore, AppStore } from '@state/AppStore';
import { Accordion, AccordionSummary, AccordionDetails, Box, Stack, Typography, IconButton, TextField, Tooltip, Divider } from '@mui/material';
import AddCircleOutlineIcon from '@mui/icons-material/AddCircleOutline';
import DeleteForeverOutlinedIcon from '@mui/icons-material/DeleteForeverOutlined';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import { useState, useEffect } from 'preact/hooks';
import { FieldsEditor } from './components/FieldsEditor';
import type { BlockTemplate } from '@core/domain/schema';
import { TemplateVariableCopier } from './components/TemplateVariableCopier';

// Block ÁºñËæëÂô®ÂÜÖÈÉ®ÁªÑ‰ª∂
function BlockEditor({ block }: { block: BlockTemplate }) {
    const [localBlock, setLocalBlock] = useState(block);
    useEffect(() => { setLocalBlock(block); }, [block]);
    const handleUpdate = (updates: Partial<BlockTemplate>) => { AppStore.instance.updateBlock(block.id, updates); };
    const handleBlur = (key: keyof BlockTemplate) => {
        // @ts-ignore
        if (localBlock[key] !== block[key]) handleUpdate({ [key]: localBlock[key] });
    };
    return (
        <Stack spacing={3}>
            <TextField label="Block ÂêçÁß∞" value={localBlock.name} onChange={e => setLocalBlock(b => ({ ...b, name: (e.target as HTMLInputElement).value }))} onBlur={() => handleBlur('name')} variant="outlined" size="small" sx={{ maxWidth: 400 }} />
            <Divider />
            <Box>
                <Typography variant="h6" sx={{ fontSize: '1rem', fontWeight: 600, mb: 1 }}>ËæìÂá∫ÁõÆÊ†á</Typography>
                <Stack spacing={2}>
                    <TextField label="ÁõÆÊ†áÊñá‰ª∂Ë∑ØÂæÑ" value={localBlock.targetFile} onChange={e => setLocalBlock(b => ({ ...b, targetFile: (e.target as HTMLInputElement).value }))} onBlur={() => handleBlur('targetFile')} placeholder="e.g., {{theme.path}}/{{Ê†áÈ¢ò.value}}.md" variant="outlined" size="small" />
                    <TextField label="ËøΩÂä†Âà∞Ê†áÈ¢ò‰∏ã (ÂèØÈÄâ)" value={localBlock.appendUnderHeader || ''} onChange={e => setLocalBlock(b => ({ ...b, appendUnderHeader: (e.target as HTMLInputElement).value }))} onBlur={() => handleBlur('appendUnderHeader')} placeholder="e.g., ## {{block.name}}" variant="outlined" size="small" />
                </Stack>
            </Box>
            <Divider />
            <Box>
                <Typography variant="h6" sx={{ fontSize: '1rem', fontWeight: 600, mb: 1.5 }}>Ë°®ÂçïÂ≠óÊÆµ</Typography>
                <FieldsEditor fields={localBlock.fields} onChange={(newFields) => handleUpdate({ fields: newFields })} />
            </Box>
            <Divider />
            <Box>
                <Stack direction="row" justifyContent="space-between" alignItems="center" sx={{ mb: 1 }}>
                    <Typography variant="h6" sx={{ fontSize: '1rem', fontWeight: 600 }}>ËæìÂá∫Ê®°Êùø</Typography>
                    <TemplateVariableCopier block={localBlock} />
                </Stack>
                {/* [‰øÆÊîπ] Á°ÆËÆ§Ë°åÊï∞ËÆæÁΩÆ‰∏∫ 8 */}
                <TextField label="Output Template" multiline rows={8} value={localBlock.outputTemplate} onChange={e => setLocalBlock(b => ({ ...b, outputTemplate: (e.target as HTMLInputElement).value }))} onBlur={() => handleBlur('outputTemplate')} placeholder="‰ΩøÁî® {{key}} ÂºïÁî®‰∏äÈù¢ÂÆö‰πâÁöÑÂ≠óÊÆµ" variant="outlined" sx={{ fontFamily: 'monospace', '& textarea': { fontSize: '13px' } }} />
            </Box>
        </Stack>
    );
}

// BlockManager ‰∏ªÁªÑ‰ª∂
export function BlockManager() {
    const blocks = useStore(state => state.settings.inputSettings.blocks);
    const [openId, setOpenId] = useState<string | null>(null);
    const handleAdd = () => {
        const newName = `Êñ∞Block ${blocks.length + 1}`;
        AppStore.instance.addBlock(newName).then(() => {
            const latestBlock = AppStore.instance.getSettings().inputSettings.blocks.at(-1);
            if (latestBlock) setOpenId(latestBlock.id);
        });
    };
    const handleDelete = (id: string, name: string) => {
        if (confirm(`Á°ÆËÆ§Âà†Èô§Block "${name}" ÂêóÔºü\nÊâÄÊúâ‰∏éÊ≠§BlockÁõ∏ÂÖ≥ÁöÑ‰∏ªÈ¢òË¶ÜÂÜôÈÖçÁΩÆÈÉΩÂ∞ÜË¢´‰∏ÄÂπ∂Âà†Èô§„ÄÇ`)) {
            AppStore.instance.deleteBlock(id);
        }
    };
    return (
        <Box sx={{ maxWidth: '900px', mx: 'auto' }}>
            <Stack direction="row" alignItems="center" spacing={1} sx={{ mb: 2 }}>
                <Typography variant="h6">1. ÁÆ°ÁêÜ Block</Typography>
                <Tooltip title="Êñ∞Â¢ûBlockÁ±ªÂûã"><IconButton onClick={handleAdd} color="success"><AddCircleOutlineIcon /></IconButton></Tooltip>
            </Stack>
            <Typography variant="body2" color="text.secondary" sx={{mb: 1.5}}>Âú®ËøôÈáåÂÆö‰πâÊâÄÊúâÂø´ÈÄüËæìÂÖ•ÁöÑÂü∫Á°ÄÊ®°ÊùøÔºå‰æãÂ¶Ç‰ªªÂä°„ÄÅÊâìÂç°„ÄÅÊÄªÁªìÁ≠â„ÄÇ</Typography>
            <Stack spacing={1}>
                {blocks.map((block, index) => (
                    <Accordion key={block.id} expanded={openId === block.id} onChange={() => setOpenId(openId === block.id ? null : block.id)} disableGutters elevation={1} sx={{ '&:before': { display: 'none' } }}>
                        <AccordionSummary>
                            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', width: '100%' }}>
                                <Typography fontWeight={500}>{block.name}</Typography>
                                <Stack direction="row" alignItems="center" spacing={0.5}>
                                    <Tooltip title="‰∏äÁßª"><span><IconButton size="small" disabled={index === 0} onClick={e => { e.stopPropagation(); AppStore.instance.moveBlock(block.id, 'up'); }}>‚ñ≤</IconButton></span></Tooltip>
                                    <Tooltip title="‰∏ãÁßª"><span><IconButton size="small" disabled={index === blocks.length - 1} onClick={e => { e.stopPropagation(); AppStore.instance.moveBlock(block.id, 'down'); }}>‚ñº</IconButton></span></Tooltip>
                                    <Tooltip title="Â§çÂà∂"><IconButton size="small" onClick={e => { e.stopPropagation(); AppStore.instance.duplicateBlock(block.id); }}><ContentCopyIcon fontSize="small" /></IconButton></Tooltip>
                                    <Tooltip title="Âà†Èô§"><IconButton size="small" onClick={e => { e.stopPropagation(); handleDelete(block.id, block.name); }} sx={{ color: 'text.secondary', '&:hover': { color: 'error.main' } }}><DeleteForeverOutlinedIcon /></IconButton></Tooltip>
                                </Stack>
                            </Box>
                        </AccordionSummary>
                        <AccordionDetails sx={{ bgcolor: 'action.hover', borderTop: '1px solid rgba(0,0,0,0.08)' }}><BlockEditor block={block} /></AccordionDetails>
                    </Accordion>
                ))}
            </Stack>
        </Box>
    );
}