// src/core/services/inputService.ts
import { App, TFile, TFolder, moment } from 'obsidian';
import type { BlockTemplate, ThemeDefinition } from '@core/domain/schema';

export class InputService {
    constructor(private app: App) { }

    // [‰øÆÊîπ] Â¢ûÂä†ËØ¶ÁªÜÁöÑË∞ÉËØïÊó•Âøó
    private renderTemplate(templateString: string, data: Record<string, any>): string {
        console.log('[ThinkÊèí‰ª∂Ë∞ÉËØï] Ê≠•È™§3: ËøõÂÖ•Ê®°ÊùøÊ∏≤ÊüìÂºïÊìé', {
            'ÂæÖÊ∏≤ÊüìÊ®°Êùø': templateString,
            'ÂèØÁî®Êï∞ÊçÆ': JSON.parse(JSON.stringify(data)),
        });

        return templateString.replace(/\{\{\s*([^}]+?)\s*\}\}/g, (_, placeholder) => {
            const key = placeholder.trim();
            let result = `(Êú™ÊâæÂà∞ÂèòÈáè: ${key})`; // ÈªòËÆ§‰∏Ä‰∏™ÈîôËØØÊèêÁ§∫ÔºåÊñπ‰æøË∞ÉËØï

            console.log(`[ThinkÊèí‰ª∂Ë∞ÉËØï]   - Ê≠£Âú®Ëß£ÊûêÂèòÈáè: {{${key}}}`);

            try {
                if (key === 'block') {
                    result = data.block?.name || '';
                } else if (key === 'theme') {
                    result = data.theme?.path || '';
                } else if (key === 'icon') {
                    result = data.theme?.icon || '';
                } else if (key.startsWith('moment:')) {
                    const format = key.substring(7);
                    result = moment().format(format);
                } else {
                    const keys = key.split('.');
                    const rootKey = keys[0];
                    
                    if (!(rootKey in data)) {
                        console.log(`[ThinkÊèí‰ª∂Ë∞ÉËØï]     -> Â§±Ë¥•: Ê†πÈîÆ "${rootKey}" Âú®Êï∞ÊçÆ‰∏≠Êú™ÊâæÂà∞„ÄÇ`);
                        return ''; // ËøîÂõûÁ©∫Â≠óÁ¨¶‰∏≤ËÄå‰∏çÊòØÈîôËØØÊèêÁ§∫
                    }

                    let value: any = data[rootKey];
                    if (keys.length > 1) {
                        for (let i = 1; i < keys.length; i++) {
                            if (value && typeof value === 'object' && keys[i] in value) {
                                value = value[keys[i]];
                            } else {
                                console.log(`[ThinkÊèí‰ª∂Ë∞ÉËØï]     -> Â§±Ë¥•: Âú®Ë∑ØÂæÑ "${key}" ‰∏≠, Êó†Ê≥ïÊâæÂà∞ "${keys[i]}"„ÄÇ`);
                                value = ''; // Ë∑ØÂæÑÊó†Êïà
                                break;
                            }
                        }
                    }

                    if (typeof value === 'object' && value !== null && 'label' in value) {
                        // Â¶ÇÊûúÊòØÁõ¥Êé•ËÆøÈóÆÂ§çÊùÇÂØπË±° {{Áä∂ÊÄÅ}}, ÈªòËÆ§ËøîÂõû label
                        result = String(value.label);
                    } else {
                        result = value !== null && value !== undefined ? String(value) : '';
                    }
                }
                console.log(`[ThinkÊèí‰ª∂Ë∞ÉËØï]     -> Ëß£ÊûêÁªìÊûú: "${result}"`);
                return result;
            } catch (e: any) {
                console.error(`[ThinkÊèí‰ª∂Ë∞ÉËØï]     -> Ëß£ÊûêÂèòÈáè {{${key}}} Êó∂ÂèëÁîüÈîôËØØ:`, e);
                return `(Ëß£ÊûêÈîôËØØ: ${key})`;
            }
        });
    }

    public async executeTemplate(template: BlockTemplate, formData: Record<string, any>, theme?: ThemeDefinition): Promise<string> {
        if (!template) throw new Error(`‰º†ÂÖ•‰∫ÜÊó†ÊïàÁöÑÊ®°ÊùøÂØπË±°„ÄÇ`);

        const renderData = { 
            ...formData, 
            block: { name: template.name },
            theme: theme ? { path: theme.path, icon: theme.icon || '' } : {}
        };
        console.log('[ThinkÊèí‰ª∂Ë∞ÉËØï] Ê≠•È™§2: ÊûÑÂª∫Áî®‰∫éÊ∏≤ÊüìÁöÑÊúÄÁªàÊï∞ÊçÆ (renderData)', JSON.parse(JSON.stringify(renderData)));

        const outputContent = this.renderTemplate(template.outputTemplate, renderData).trim();
        console.log(`[ThinkÊèí‰ª∂Ë∞ÉËØï] Ê≠•È™§4: ÊúÄÁªàÊ∏≤ÊüìÂá∫ÁöÑÂæÖÂÜôÂÖ•ÂÜÖÂÆπ:\n---\n${outputContent}\n---`);
        
        const targetFilePath = this.renderTemplate(template.targetFile, renderData).trim();
        console.log(`[ThinkÊèí‰ª∂Ë∞ÉËØï] Ê≠•È™§5: ÊúÄÁªàÊ∏≤ÊüìÂá∫ÁöÑÁõÆÊ†áÊñá‰ª∂Ë∑ØÂæÑ: "${targetFilePath}"`);

        const header = template.appendUnderHeader ? this.renderTemplate(template.appendUnderHeader, renderData) : null;
        if(header) console.log(`[ThinkÊèí‰ª∂Ë∞ÉËØï] Ê≠•È™§6: ÊúÄÁªàÊ∏≤ÊüìÂá∫ÁöÑÁõÆÊ†áÊ†áÈ¢ò: "${header}"`);

        if (!targetFilePath) throw new Error("Ê®°ÊùøÊú™ÂÆö‰πâÁõÆÊ†áÊñá‰ª∂Ë∑ØÂæÑ (targetFile)„ÄÇ");
        
        const file = await this.getOrCreateFile(targetFilePath);
        if (header) {
            await this.appendUnderHeader(file, header, outputContent);
        } else {
            const existingContent = await this.app.vault.read(file);
            const newContent = existingContent ? `${existingContent.trim()}\n\n${outputContent}` : outputContent;
            await this.app.vault.modify(file, newContent);
        }
        console.log('[ThinkÊèí‰ª∂Ë∞ÉËØï] Ê≠•È™§7: Êñá‰ª∂ÂÜôÂÖ•Êìç‰ΩúÂÆåÊàê„ÄÇ');
        return targetFilePath;
    }

    // ... ÂÖ∂‰ΩôËæÖÂä©ÂáΩÊï∞‰øùÊåÅ‰∏çÂèò ...
    private async ensureFolder(path: string): Promise<void> {
        const segs = path.split('/').filter(Boolean);
        let cur = '';
        for (const seg of segs) {
            cur = cur ? `${cur}/${seg}` : seg;
            const af = this.app.vault.getAbstractFileByPath(cur);
            if (!af) {
                try {
                    await this.app.vault.createFolder(cur);
                } catch (e: any) {
                    throw new Error(`ÂàõÂª∫Êñá‰ª∂Â§π "${cur}" Â§±Ë¥•„ÄÇËØ∑Ê£ÄÊü•Ë∑ØÂæÑÊòØÂê¶ÊúâÊïàÊàñÂ≠òÂú®ÊùÉÈôêÈóÆÈ¢ò„ÄÇÂéüÂßãÈîôËØØ: ${e.message}`);
                }
            } else if (af instanceof TFile) {
                throw new Error(`Ë∑ØÂæÑÂÜ≤Á™ÅÔºö"${cur}" ÊòØ‰∏Ä‰∏™Êñá‰ª∂ÔºåÊó†Ê≥ïÂú®ÂÖ∂‰∏ãÂàõÂª∫Êñá‰ª∂Â§π„ÄÇ`);
            }
        }
    }
    private async getOrCreateFile(fp: string): Promise<TFile> {
        let file = this.app.vault.getAbstractFileByPath(fp);
        if (file instanceof TFile) {
            return file;
        }
        if (file instanceof TFolder) {
            throw new Error(`ÁõÆÊ†áË∑ØÂæÑ "${fp}" ÊòØ‰∏Ä‰∏™Êñá‰ª∂Â§πÔºåÊó†Ê≥ïÂàõÂª∫Êñá‰ª∂„ÄÇ`);
        }
        const folder = fp.includes('/') ? fp.slice(0, fp.lastIndexOf('/')) : '';
        if (folder) {
            await this.ensureFolder(folder);
        }
        try {
            return await this.app.vault.create(fp, '');
        } catch (error: any) {
            console.warn(`ThinkPlugin: ÂàõÂª∫Êñá‰ª∂Êó∂ÈÅáÂà∞ÂàùÂßãÈîôËØØ: ${error.message}. Ê≠£Âú®Â∞ùËØïÈáçÊñ∞Ëé∑Âèñ...`);
            await new Promise(resolve => setTimeout(resolve, 100));
            const existingFile = this.app.vault.getAbstractFileByPath(fp);
            if (existingFile instanceof TFile) {
                return existingFile;
            } else {
                console.error("ThinkPlugin: Êñá‰ª∂ÂàõÂª∫Â§±Ë¥•ÔºåÂπ∂‰∏îÂú®ÊçïËé∑ÈîôËØØÂêé‰ªçÊó†Ê≥ïÊâæÂà∞ËØ•Êñá‰ª∂„ÄÇ", error);
                throw new Error(`ÂàõÂª∫Êñá‰ª∂ "${fp}" Â§±Ë¥•„ÄÇÂéüÂßãÈîôËØØ: ${error.message}`);
            }
        }
    }
    private async appendUnderHeader(file: TFile, header: string, payload: string) {
        const esc = header.replace(/[.*+?^${}()|[\]\\]/g, '\\$&');
        const regex = new RegExp(`^${esc}\\s*$`, 'm');
        const text = await this.app.vault.read(file);
        const lines = text.split('\n');
        let headerLineIndex = lines.findIndex(l => regex.test(l));
        if (headerLineIndex === -1) {
            if (lines.length && lines[lines.length - 1].trim() !== '') lines.push('');
            lines.push(header, '');
            headerLineIndex = lines.length - 2;
        }
        let insertAtIndex = lines.length;
        for (let i = headerLineIndex + 1; i < lines.length; i++) {
            const match = lines[i].match(/^(#+)\s/);
            if (match && match[1].length <= (header.match(/^(#+)\s/)?.[1].length || 0)) {
                insertAtIndex = i;
                break;
            }
        }
        if (insertAtIndex > 0 && lines[insertAtIndex - 1].trim() !== '') {
            lines.splice(insertAtIndex, 0, '', payload);
        } else {
            lines.splice(insertAtIndex, 0, payload);
        }
        await this.app.vault.modify(file, lines.join('\n'));
    }
}