// src/core/services/RendererService.ts
import { h, render } from 'preact';
import { Layout } from '@core/domain/schema';
import { DataStore } from '@core/services/dataStore';
import { AppStore } from '@state/AppStore';
import { LayoutRenderer } from '@features/dashboard/ui/LayoutRenderer';
import type ThinkPlugin from '../../main';

interface ActiveLayout {
    container: HTMLElement;
    layoutName: string;
}

/**
 * @class RendererService
 * @description Âçï‰æãÊúçÂä°ÔºåË¥üË¥£ÁÆ°ÁêÜÊâÄÊúâÊ¥ªÂä®ËßÜÂõæÔºà‰æãÂ¶ÇÂú®‰ª£Á†ÅÂùó‰∏≠Ê∏≤ÊüìÁöÑÂ∏ÉÂ±ÄÔºâÁöÑÁîüÂëΩÂë®Êúü„ÄÇ
 * 1. Ê≥®ÂÜåÂíåÊ≥®ÈîÄÊ¥ªÂä®Â∏ÉÂ±Ä„ÄÇ
 * 2. ËÆ¢ÈòÖ AppStore ÁöÑÁä∂ÊÄÅÂèòÂåñ„ÄÇ
 * 3. ÂΩìËÆæÁΩÆÂèòÂåñÊó∂ÔºåËá™Âä®ÈáçÊñ∞Ê∏≤ÊüìÊâÄÊúâÂèóÂΩ±ÂìçÁöÑÂ∏ÉÂ±Ä„ÄÇ
 * 4. Âú®Êèí‰ª∂Âç∏ËΩΩÊó∂ÔºåÁªü‰∏ÄÊ∏ÖÁêÜÊâÄÊúâÊ∏≤ÊüìÁöÑÁªÑ‰ª∂„ÄÇ
 */
export class RendererService {
    static instance: RendererService;

    private activeLayouts: ActiveLayout[] = [];

    // [Êñ∞Â¢û] ‰æùËµñÊ≥®ÂÖ• DataStore Âíå AppStore
    constructor(
        private plugin: ThinkPlugin, // ‰æùÁÑ∂ÈúÄË¶Å plugin ÂÆû‰æãÊù•‰º†ÈÄíÁªô LayoutRenderer
        private dataStore: DataStore,
        private appStore: AppStore,
    ) {
        if (RendererService.instance) {
            return RendererService.instance;
        }
        
        // [Ê†∏ÂøÉ] ËÆ¢ÈòÖ AppStore ÁöÑÂèòÂåñÔºåÂΩìËÆæÁΩÆÊõ¥Êñ∞Êó∂Ëá™Âä®Ë∞ÉÁî® rerenderAll
        this.appStore.subscribe(() => this.rerenderAll());
        RendererService.instance = this;
    }

    /**
     * Ê≥®ÂÜå‰∏Ä‰∏™Êñ∞ÁöÑÂ∏ÉÂ±ÄÂÆû‰æãÔºåËøõË°åÂàùÊ¨°Ê∏≤ÊüìÂπ∂Á∫≥ÂÖ•ÁÆ°ÁêÜ„ÄÇ
     * @param container - ÊâøËΩΩPreactÁªÑ‰ª∂ÁöÑHTMLÂÖÉÁ¥†„ÄÇ
     * @param layout - Ë¶ÅÊ∏≤ÊüìÁöÑÂ∏ÉÂ±ÄÈÖçÁΩÆ„ÄÇ
     */
    public register(container: HTMLElement, layout: Layout): void {
        console.log(`[RendererService] Registering layout: "${layout.name}"`);
        // Á°Æ‰øùÂêå‰∏Ä‰∏™ÂÆπÂô®‰∏ç‰ºöË¢´ÈáçÂ§çÊ≥®ÂÜå
        this.unregister(container);

        // Ê∏≤ÊüìÁªÑ‰ª∂
        render(
            h(LayoutRenderer, {
                layout: layout,
                dataStore: this.dataStore,
                plugin: this.plugin,
            }),
            container,
        );

        // Ê∑ªÂä†Âà∞Ê¥ªÂä®Â∏ÉÂ±ÄÂàóË°®
        this.activeLayouts.push({ container, layoutName: layout.name });
    }

    /**
     * Ê≥®ÈîÄ‰∏Ä‰∏™Â∏ÉÂ±ÄÂÆû‰æãÔºå‰ªéDOM‰∏≠Âç∏ËΩΩÁªÑ‰ª∂Âπ∂ÁßªÂá∫ÁÆ°ÁêÜÂàóË°®„ÄÇ
     * @param container - ÁõÆÊ†áHTMLÂÖÉÁ¥†„ÄÇ
     */
    public unregister(container: HTMLElement): void {
        const index = this.activeLayouts.findIndex(l => l.container === container);
        if (index > -1) {
            const { layoutName } = this.activeLayouts[index];
            console.log(`[RendererService] Unregistering layout: "${layoutName}"`);
            
            try {
                render(null, container); // Âç∏ËΩΩ Preact ÁªÑ‰ª∂
            } catch (e) {
                // ÊçïËé∑ÂºÇÂ∏∏Ôºå‰ª•Èò≤ÂÆπÂô®Â∑≤Ë¢´ObsidianÈîÄÊØÅ
            }
            container.empty(); // Ê∏ÖÁ©∫ÂÆπÂô®
            
            this.activeLayouts.splice(index, 1);
        }
    }

    /**
     * ÂΩìËÆæÁΩÆÂèòÂåñÊó∂ÔºåÈáçÊñ∞Ê∏≤ÊüìÊâÄÊúâÂΩìÂâçÊ¥ªÂä®ÁöÑÂ∏ÉÂ±Ä„ÄÇ
     */
    private rerenderAll(): void {
        console.log('[RendererService] Settings changed, rerendering all active layouts...');
        const latestSettings = this.appStore.getSettings();

        // ‰ΩøÁî® for...of Âæ™ÁéØÊù•Â§ÑÁêÜÔºåÂõ†‰∏∫ÂÆÉÊØî forEach Êõ¥Êòì‰∫éÈòÖËØªÂíåË∞ÉËØï
        for (const activeLayout of [...this.activeLayouts]) {
            const { container, layoutName } = activeLayout;
            const newLayoutConfig = latestSettings.layouts.find(l => l.name === layoutName);

            if (newLayoutConfig) {
                // Â¶ÇÊûúÂ∏ÉÂ±ÄÈÖçÁΩÆ‰æùÁÑ∂Â≠òÂú®ÔºåÂàôÈáçÊñ∞Ê∏≤Êüì
                console.log(`  - Rerendering "${layoutName}"`);
                render(
                    h(LayoutRenderer, {
                        layout: newLayoutConfig,
                        dataStore: this.dataStore,
                        plugin: this.plugin,
                    }),
                    container,
                );
            } else {
                // Â¶ÇÊûúÂ∏ÉÂ±ÄÈÖçÁΩÆË¢´Âà†Èô§‰∫ÜÔºåÂàôÊ≥®ÈîÄÂπ∂ÊòæÁ§∫ÊèêÁ§∫‰ø°ÊÅØ
                console.log(`  - Layout "${layoutName}" was deleted, unregistering.`);
                this.unregister(container);
                container.createDiv({ text: `Â∏ÉÂ±ÄÈÖçÁΩÆ "${layoutName}" Â∑≤Ë¢´Âà†Èô§„ÄÇ` });
            }
        }
    }
    
    /**
     * Âú®Êèí‰ª∂Âç∏ËΩΩÊó∂Ë∞ÉÁî®ÔºåÊ∏ÖÁêÜÊâÄÊúâÊ¥ªÂä®ÁöÑÂ∏ÉÂ±Ä„ÄÇ
     */
    public cleanup(): void {
        console.log('[RendererService] Cleaning up all active layouts.');
        // ÂàõÂª∫‰∏Ä‰∏™ÂâØÊú¨ËøõË°åËø≠‰ª£ÔºåÂõ†‰∏∫ unregister ‰ºö‰øÆÊîπÂéüÂßãÊï∞ÁªÑ
        [...this.activeLayouts].forEach(layout => this.unregister(layout.container));
        this.activeLayouts = [];
    }
}