// src/core/utils/normalize.ts
// Áªü‰∏Ä date/dateMs/dateSourceÔºàcategoryKey Â∑≤Áî± parser ÂÜ≥ÂÆöÔºõÊ≠§Â§ÑÂè™ÂÖúÂ∫ïÔºâ

import { Item } from '@core/domain/schema';

const ORDER = ['done','due','scheduled','start','created','end'] as const;
type DateKey = typeof ORDER[number];

/** Áªü‰∏ÄËÆ°ÁÆóÂá∫ date/dateMs/dateSourceÔºõÂπ∂ÂÖúÂ∫ï categoryKey */
export function normalizeItemDates(it: Item): void {
  // BlockÔºöÂéüÊú¨Â∞±Êúâ date
  if (it.type === 'block') {
    if (it.date) {
      it.dateSource = 'block';
      const t = Date.parse(it.date);
      if (!isNaN(t)) it.dateMs = t;
    }
    // ÂÖúÂ∫ïÔºöÊ≤°ÊúâÂ∞±Áî®Á©∫Â≠óÁ¨¶‰∏≤ÔºàÁêÜËÆ∫‰∏ä parser ‰ºöÁªôÂà∞Ôºâ
    if (!it.categoryKey) (it as any).categoryKey = '';
    return;
  }

  // TaskÔºöÊåâ‰ºòÂÖàÁ∫ßÊé®ÂØºÁªü‰∏ÄÊó•Êúü
  const pick: Record<DateKey, string | undefined> = {
    done     : it.doneDate,
    due      : it.dueDate,
    scheduled: it.scheduledDate,
    start    : it.startDate ?? it.startISO,
    created  : it.createdDate,
    end      : it.endISO,
  };

  for (const k of ORDER) {
    const iso = pick[k];
    if (iso) {
      it.date = iso;
      it.dateSource = k;
      const t = Date.parse(iso);
      if (!isNaN(t)) it.dateMs = t;
      break;
    }
  }

  // ÂÖúÂ∫ï categoryKeyÔºö‰ªªÂä°ÈªòËÆ§ open
  if (!it.categoryKey) (it as any).categoryKey = '‰ªªÂä°/open';
}