// src/core/CodeblockEmbedder.ts
import { h, render } from 'preact';
import { Notice, Plugin } from 'obsidian';
import { CODEBLOCK_LANG } from '@core/domain/constants';
import { DataStore } from '@core/services/dataStore';
import { LayoutRenderer } from '@features/dashboard/ui/LayoutRenderer';
import { AppStore } from '@state/AppStore';
import { RendererService } from '@core/services/RendererService'; // [‰øÆÊîπ] ÂØºÂÖ•Êñ∞ÊúçÂä°
import type { Layout } from '@core/domain/schema';

export class CodeblockEmbedder {
    // [‰øÆÊîπ] ÊûÑÈÄ†ÂáΩÊï∞‰æùËµñÊ≥®ÂÖ•Ôºå‰∏çÂÜçÈúÄË¶ÅÂÆåÊï¥ÁöÑ ThinkPlugin ÂÆû‰æã
    constructor(
        private plugin: Plugin, // Âè™ÈúÄË¶Å Plugin Âü∫Á±ªÊù•Ê≥®ÂÜåÂ§ÑÁêÜÂô®
        private appStore: AppStore,
        private dataStore: DataStore,
        private rendererService: RendererService,
    ) {
        this.registerProcessor();
    }

    private registerProcessor() {
        this.plugin.registerMarkdownCodeBlockProcessor(
            CODEBLOCK_LANG,
            (source, el, /* ctx */) => {
                // Ê∏ÖÁêÜÊóßÁöÑÊ∏≤Êüì
                try { render(null, el); } catch { }
                el.empty();
                
                let layoutName: string | undefined;

                // Ëß£Êûê‰ª£Á†ÅÂùóÂÜÖÂÆπÔºåËé∑ÂèñÂ∏ÉÂ±ÄÂêçÁß∞
                try {
                    const trimmedSource = source.trim();
                    if (trimmedSource.startsWith('{')) {
                        const input = JSON.parse(trimmedSource);
                        layoutName = input.layout || input.name;
                    } else if (trimmedSource) {
                        layoutName = trimmedSource.replace(/['"]/g, '');
                    }
                } catch (e) {
                    console.warn('ThinkPlugin: ‰ª£Á†ÅÂùóÂÜÖÂÆπËß£ÊûêÂ§±Ë¥•', e);
                    el.createDiv({ text: '‰ª£Á†ÅÂùóÂÜÖÂÆπËß£ÊûêÂ§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ËØ≠Ê≥ï„ÄÇÂ∫î‰∏∫Â∏ÉÂ±ÄÂêçÁß∞ÊàñJSON„ÄÇ' });
                    return;
                }
                
                // [‰øÆÊîπ] ‰ªé AppStore Ëé∑ÂèñÊúÄÊñ∞ÁöÑÂ∏ÉÂ±ÄÈÖçÁΩÆ
                const allLayouts = this.appStore.getSettings().layouts;

                if (!layoutName && allLayouts.length > 0) {
                    layoutName = allLayouts[0].name;
                    new Notice(`Think Plugin: Êú™ÊåáÂÆöÂ∏ÉÂ±ÄÔºåÂ∑≤Ëá™Âä®ÈÄâÊã©Á¨¨‰∏Ä‰∏™Â∏ÉÂ±Ä "${layoutName}"„ÄÇ`);
                }

                const layout = allLayouts.find(l => l.name === layoutName);
                if (!layout) {
                    el.createDiv({ text: `Think Plugin: Êâæ‰∏çÂà∞ÂêçÁß∞‰∏∫ "${layoutName}" ÁöÑÂ∏ÉÂ±Ä„ÄÇËØ∑Âú®Êèí‰ª∂ËÆæÁΩÆ‰∏≠ÂàõÂª∫„ÄÇ` });
                    return;
                }

                // [‰øÆÊîπ] ÊåÇËΩΩÂπ∂Ê≥®ÂÜåÂà∞ RendererService
                this.mountAndRegister(el, layout);
            },
        );
    }

    /**
     * [‰øÆÊîπ] Â∞ÜÁªÑ‰ª∂ÊåÇËΩΩÂà∞ÂÖÉÁ¥†‰∏äÔºåÂπ∂Â∞ÜÂÖ∂Ê≥®ÂÜåÂà∞ RendererService ËøõË°åÁîüÂëΩÂë®ÊúüÁÆ°ÁêÜ„ÄÇ
     */
    private mountAndRegister(el: HTMLElement, layout: Layout) {
        // [‰øÆÊîπ] Ë∞ÉÁî® RendererService ËøõË°åÊ≥®ÂÜåÂíåÊ∏≤ÊüìÔºå‰∏çÂÜçÁõ¥Êé•Êìç‰Ωú activeLayouts Êï∞ÁªÑ
        this.rendererService.register(el, layout);
    }
}